import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageComponent } from './pages/home-page/home-page.component';
import { LiveStreamComponent } from './pages/live-stream/live-stream.component';
import { ProgramsScheduleComponent } from './pages/programs-schedule/programs-schedule.component';
import { ProgramsComponent } from './pages/programs/programs.component';
import { ArticlesComponent } from './pages/articles/articles.component';
import { SportsComponent } from './pages/sports/sports.component';
import { VideoComponent } from './pages/video/video.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { ContactUsComponent } from './pages/contact-us/contact-us.component';
import { TeamComponent } from './pages/team/team.component';
import { ArticleDetailsComponent } from './pages/article-details/article-details.component';
import { ProgramDetailsComponent } from './pages/program-details/program-details.component';
import { PrivacyComponent } from './pages/privacy/privacy.component';
import { TermsComponent } from './pages/terms/terms.component';
import { SearchComponent } from './pages/search/search.component';
import { TeamDetailComponent } from './pages/team-detail/team-detail.component';
import { MetaGuard } from '@ngx-meta/core';



const routes: Routes = [
  {
    path: '', canActivateChild: [MetaGuard], component: HomePageComponent
  },
  { path: 'live', canActivateChild: [MetaGuard], component: LiveStreamComponent, },
  { path: 'schedule', canActivateChild: [MetaGuard], component: ProgramsScheduleComponent },
  { path: 'programs', canActivateChild: [MetaGuard], component: ProgramsComponent },
  { path: 'articles', canActivateChild: [MetaGuard], component: ArticlesComponent },
  { path: 'sports', canActivateChild: [MetaGuard], component: SportsComponent },
  { path: 'video', canActivateChild: [MetaGuard], component: VideoComponent },
  { path: 'aboutUs', canActivateChild: [MetaGuard], component: AboutUsComponent },
  { path: 'privacy', canActivateChild: [MetaGuard], component: PrivacyComponent },
  { path: 'terms', canActivateChild: [MetaGuard], component: TermsComponent },
  { path: 'contactUs', canActivateChild: [MetaGuard], component: ContactUsComponent },
  { path: 'team', canActivateChild: [MetaGuard], component: TeamComponent },
  { path: 'articleDetails/:type/:id', canActivateChild: [MetaGuard], component: ArticleDetailsComponent },
  { path: 'program/:id', canActivateChild: [MetaGuard], component: ProgramDetailsComponent },
  { path: 'search', canActivateChild: [MetaGuard], component: SearchComponent },
  { path: 'team-detail/:id', canActivateChild: [MetaGuard], component: TeamDetailComponent },


];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
