import { Component, OnInit, OnDestroy, ViewEncapsulation } from "@angular/core";
import { ScheduleService } from "src/app/services/uiServices/schedule.service";
declare var $: any;
declare var mixitup: any;
import * as moment from "moment";
import { HomePageServicesService } from "src/app/services/uiServices/home-page-services.service";
var mixer: any;
import { DomSanitizer } from "@angular/platform-browser";
import { Constants } from "src/app/Constants";
import { ServiceHandlerProvider } from "src/app/services/service-handler.service";

@Component({
  selector: "app-search",
  templateUrl: "./search.component.html",
  styleUrls: ["./search.component.css"],
  encapsulation: ViewEncapsulation.None
})
export class SearchComponent implements OnInit, OnDestroy {
  articles: Array<Article>;
  originalArticles: Array<Article>;
  vidoes: Array<any>;
  searchKeyword: string = "";
  setttings: Settings;
  channelId: string;
  articlesCount: number;
  showVideos: boolean = false;
  constructor(
    private homeService: HomePageServicesService,
    private sanitizer: DomSanitizer,
    private serviceHandeler: ServiceHandlerProvider
  ) {
    this.articlesCount = 5;
  }

  async ngOnInit() {
    this.setttings = await this.homeService.getSettings();
    this.channelId = this.setttings.channelId;
    // console.log("Youtube channel id: " + this.channelId);

  }
  displayVideos(displayVid: boolean) {
    this.showVideos = displayVid;
  }
  async loadMore() {
    this.articlesCount = this.articlesCount + 5;
    this.articles = this.originalArticles.slice(0, this.articlesCount);

    // if (mixer != undefined) {
    //   console.log(mixer);
    //   console.log(mixer.forceRender);
    //   mixer.forceRender();
    // }
  }
  async searchArticles() {
    this.originalArticles = await this.homeService.searchArticles(this.searchKeyword);
    this.originalArticles = this.originalArticles.map(article => {
      let newArticle: any = article;
      newArticle.article = this.sanitizer.bypassSecurityTrustHtml(
        article.article
      );
      return newArticle;
    });
    this.articles = this.originalArticles.slice(0, this.articlesCount);
    // console.log(this.originalArticles);
  }
  async searchVidoes() {
    // console.log("search videos");

    let params = {
      part: "snippet",
      maxResults: "12",
      channelId: this.channelId,
      type: "video",
      q: this.searchKeyword,
      key: Constants.YOUTUBE_API_KEY
    };
    this.serviceHandeler
      .runService(
        "https://www.googleapis.com/youtube/v3/search",
        "GET",
        null,
        null,
        null,
        params
      )
      .subscribe(
        response => {
          // console.log(response);
          this.vidoes = response.items;
          // console.log(this.vidoes);
          // $(document).ready(function () {
          //   mixer = mixitup("#Container", {
          //     load: {
          //       filter: ".Sub"
          //     }
          //   });
          // });
        },
        error => {
          // console.log(error);
        }
      );

  }
  async search() {
    if (mixer != undefined) {
      mixer.destroy();
    }
    await this.searchArticles();
    await this.searchVidoes();

  }
  ngOnDestroy() {
    if (mixer != undefined) {
      mixer.destroy();
    }
  }
  getSharingLink(articleId: string, articleType: string): string {
    return Constants.SHARING_URL + "/articleDetails/" + articleType + "/" + articleId;
  }
  translateDate(date: string): string {
    moment.locale('ar');
    return moment(date).format("YYYY/MM/DD");
  }
}
